			</card>
			
			<!-- 按钮 -->
			<view class="d-flex position-fixed bottom-0 right-0 w-100">
				<view class="flex-1 main-bg-color font-md text-white py-2 text-center" hover-class="main-bg-hover-color">
					确定
				</view>
				<view class="flex-1 font-md py-2 text-center bg-light-secondary" hover-class="main-bg-hover-color">
					重置
				</view>
			</view>
		</uni-drawer>
		
		<!-- 列表 -->
		<block v-for="(item,index) in list" :key="index">
			<goods-list :item="item"></goods-list>
		</block>
	</view>
</template>

