						{name:"选项4"},
					]
				},
				list:[
					{
						title:'真无线蓝牙耳机',
						titlepic:'../../static/images/demo/demo5.jpg',
						desc:'雅致简约 / 分体式入耳 / 收纳盒充电 / 蓝牙5.0 / 触控操作',
						price:'1399',
						comment_num:1300,
						good_num:97
					},
					{
						title:'真无线蓝牙耳机',
